@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,500;0,900;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bigelow+Rules&display=swap);
:root{
  --light : #e3e3e3;
  --dark: #282c34;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  /* background:linear-gradient(to left , var(--dark) , var(--light)); */
  background: #282c34;
  background: var(--dark);
  font-family: sans-serif;
  background-color: #282c34 !important;
  background-color: var(--dark) !important;
  color: #e3e3e3 !important;
  color: var(--light) !important;
}


.App {
    text-align: center;
}

h1{
    font-family: 'Bigelow Rules', cursive;
    font-size: 72px;
    color: #61dafb;
    padding: 30px 0;
}
.card{
    text-align: center;
    padding: 20px;
    background: #0078d4;
    border-radius: 10px;
    border: 1px solid #494c50;
    transition: -webkit-transform 0.25s ease-in;
    transition: transform 0.25s ease-in;
    transition: transform 0.25s ease-in, -webkit-transform 0.25s ease-in;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    cursor: pointer;
}

.card:hover{
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
}

h2{
    padding: 15px 0;
}
.card-list{
    width: 85vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
}
.search {
    padding: 10px 20px;
    margin-bottom: 30px;
    border: 1px solid #ccc;
    border-radius: 10px;
    font-size: 1.1rem;
    color: #333;
    outline: none;
}
